* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#root {
  background-color: green;
}

.App {
    width: 100%;

    .header {
        width: 100%;
        background-color: green;
        height: 75px;
        position: fixed;
        display: none;
    }
    main {
        width: 100%;
        // height: 100vh;
        // min-height: -webkit-fill-available;
        // height: 100vh;
        // max-height: -webkit-fill-available;
        height: 100dvh;
        background-color: rgb(48, 14, 14);
        font-size: 3.25rem;
        text-align: center;
        color: white;
        vertical-align: middle;
    }
    .footer {
        display: none;
        width: 100%;
        background-color: wheat;
        height: 75px;
        position: fixed;
        bottom: 0;
        left: 0;
    }
}
